html,
body {
  font-size: 16px;
  margin: 0;
  font-family: "minion-pro", "proxima-nova", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  font-style: normal;
  letter-spacing: 0.02em;
  text-transform: none;
  line-height: 1.8em;
  font-size: 1rem;
  line-height: 1.6;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div {
  box-sizing: border-box;
}

.active {
  color: #e6007e;
}

.inactive:hover {
  color: #f390c6;
}
